@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "Comfortaa";
  src: url(../fonts/Comfortaa/Comfortaa-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Comfortaa";
  src: url(../fonts/Comfortaa/Comfortaa-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-duotone.ttf);
  font-weight: 900;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-light.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Medium.ttf);
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Regular.ttf);
  font-weight: 400;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

a,
button:hover {
  cursor: pointer;
}

button::after,
button::before:hover {
  cursor: pointer;
}

ul {
  list-style: none;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.d-flex {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.bg-image {
  position: absolute;
  z-index: 0;
  width: 100%;
  min-height: 100vh;
  object-fit: cover;
  filter: blur(1.5px);
  filter: brightness(30%);
}

main {
  position: relative;
}

.card-content {
  min-height: 100vh;
  padding-left: 25%;
  padding-right: 25%;
}

@media (max-width: 768px) {
  .card-content {
    padding-left: 15%;
    padding-right: 15%;
  }
}

@media (max-width: 500px) {
  .card-content {
    padding-left: 10%;
    padding-right: 10%;
  }
}

.card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 900px;
  width: 100%;
}

.card__main {
  position: absolute;
  z-index: 1;
  background: url("../../../images/main-card-bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: brightness(80%);
  width: 100%;
  min-height: 70vh;
  box-shadow: inset 0 0 30px 2rem rgba(0, 0, 0, 0.534);
}

@media (max-width: 992px) {
  .card__main {
    min-height: 50vh;
  }
}

@media (max-width: 500px) {
  .card__main {
    min-height: 45vh;
  }
}

@media (max-width: 380px) {
  .card__main {
    min-height: 40vh;
  }
}

.card__first, .card__second {
  z-index: 99;
  width: 100%;
  min-height: 70vh;
  background: url("../../../images/christmas-tree.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(0.5px);
  filter: brightness(80%);
  border: 2px solid #333;
  transition: transform 2s;
}

@media (max-width: 992px) {
  .card__first, .card__second {
    min-height: 50vh;
  }
}

@media (max-width: 500px) {
  .card__first, .card__second {
    min-height: 45vh;
    width: 90%;
  }
}

@media (max-width: 380px) {
  .card__first, .card__second {
    min-height: 40vh;
  }
}

.card-left {
  transform-origin: 0 50%;
}

.card-right {
  transform-origin: 100% 50%;
}

.card:hover .card-left {
  transform: perspective(700px) rotateY(-180deg);
}

@media (max-width: 768px) {
  .card:hover .card-left {
    transform: perspective(600px) rotateY(-120deg);
  }
}

.card:hover .card-right {
  transform: perspective(700px) rotateY(180deg);
}

@media (max-width: 768px) {
  .card:hover .card-right {
    transform: perspective(600px) rotateY(120deg);
  }
}
