* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

a,
button:hover {
    cursor: pointer;
}

button::after,
button::before:hover {
    cursor: pointer;
}

ul {
    list-style: none;
}

.container {
    @include container();
}

.row {
    @include row();
}

.d-flex {
    display: flex;
}

.flex-column {
    flex-direction: column;
}

.justify-center {
    justify-content: center;
}

.align-center {
    align-items: center;
}

.bg-image {
    position: absolute;
    z-index: 0;
    width: 100%;
    min-height: 100vh;
    object-fit: cover;
    filter: blur(1.5px); 
    filter: brightness(30%);
}

main {
    position: relative;
}