.card-content {
    min-height: 100vh;
    padding-left: 25%;
    padding-right: 25%;

    @include media($sm) {
        padding-left: 15%;
        padding-right: 15%;
    }

    @include media(500px) {
        padding-left: 10%;
        padding-right: 10%;
    }
}

.card {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 900px;
    width: 100%;

    &__main {
        position: absolute;
        z-index: 1;
        background: url('../../../images/main-card-bg.jpg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        filter: brightness(80%);
        width: 100%;
        min-height: 70vh;
        box-shadow: inset 0 0 30px 2rem rgba(0, 0, 0, 0.534);
   
        @include media($md) {
            min-height: 50vh;
        } 
 
        @include media(500px) { 
            min-height: 45vh;
        }

        @include media(380px) {
            min-height: 40vh;
        }
    }

    &__first,
    &__second {
        z-index: 99;
        width: 100%;
        min-height: 70vh;
        background: url('../../../images/christmas-tree.jpg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        filter: blur(0.5px);  
        filter: brightness(80%);
        border: 2px solid #333;
        transition: transform 2s;

        @include media($md) {
            min-height: 50vh;
        }

        @include media(500px) {
            min-height: 45vh;
            width: 90%;
        }

        @include media(380px) {
            min-height: 40vh;
        } 
    }

    &-left {
        transform-origin: 0 50%;
    }

    &-right {
        transform-origin: 100% 50%;
    }

    &:hover &-left {
        transform: perspective(700px) rotateY(-180deg);

        @include media($sm) {
            transform: perspective(600px) rotateY(-120deg);
        }
    }

    &:hover &-right {
        transform: perspective(700px) rotateY(180deg);

        @include media($sm) {
            transform: perspective(600px) rotateY(120deg);
        }
    }
}